.navContainer {
  display: flex;
  position: relative;
  align-items: center;
  justify-content: space-between;
  height: 60px;
  padding: 18px 20px;
  background-color: var(--secondaryBgColor);
}

.buttonBurger {
  width: 24px;
  height: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--secondaryBgColor);
}

.iconBurger {
  border-radius: 2px;
  display: flex;
  justify-content: center;
  align-items: center;
  stroke: var(--mainTextColor);
  cursor: pointer;
}

.navMenu {
  display: flex;
  align-items: center;
  gap: 14px;
}

@media screen and (min-width: 768px) {
  .navContainer {
    height: 68px;
    padding: 18px 32px;
  }

  .buttonBurger {
    width: 32px;
    height: 32px;
  }

  .iconBurger {
    width: 24px;
    height: 24px;
  }
}

.spinner{
  position: absolute;
  left: 80px;
}

@media screen and (min-width: 1440px) {
  .navContainer {
    justify-content: flex-end;
  }

  .buttonBurger {
    display: none;
  }

  .spinner{
    position: absolute;
    left: 20px;
  }
}

.lngWrap{
  display: flex;
  align-items: center;
  gap: 5px;
}

.lngBtn{
  color: var(--secondaryTextColor);
  background-color: inherit;
  padding: 4px;
  border-radius: 5px;
  cursor: pointer;

}

.lngBtn:hover{
  background-color: var(--lightBgColor);
  color: var(--mainTextColor);
  fill: var(--mainTextColor)
}

.iconLng{
  stroke: var(--mainTextColor);
  fill: var(--secondaryTextColor) ;
}
